.boringmenu {
  margin: 0;
  padding: 0;
}

.boringmenu__list-item {
  list-style-type: none;
  position: relative;
}

.boringmenu__toggle {
  color: #fff;
  cursor: pointer;
  background: #333;
  border: 0;
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  top: .35rem;
  right: 1rem;
}

.boringmenu__item {
  border-bottom: .0625rem solid #ddd;
  padding: 1rem 4.5rem 1rem 1rem;
  display: block;
}

.boringmenu__item:focus, .boringmenu__item:hover {
  text-decoration: underline;
}

.boringmenu__item:focus {
  outline: none;
  box-shadow: inset 0 0 0 .125rem #00f;
}

.boringmenu__item--level-1 {
  background: #eee;
}

.boringmenu__item--level-2 {
  padding-left: 1.5rem;
}

.boringmenu__item--level-3 {
  padding-left: 2.5rem;
}

.boringmenu__item--level-4 {
  padding-left: 3.5rem;
}

.boringmenu__item--level-5 {
  padding-left: 4.5rem;
}

.boringmenu__item--parent {
  background: #eee;
  text-decoration: underline;
}

.boringmenu__item--current {
  text-decoration: underline;
}

.boringmenu__sr-only {
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border-width: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.boringmenu__hidden {
  display: none;
}
